import React, {Fragment} from 'react';
import EmployeeImageLink from '../../components/EmployeeImageLink';
import FullPageImageWithHeader from '../../components/FullPageImageWithHeader';
import Navigation from '../../components/Navigation';
import SmartPeople from '../../images/smart-people.jpg';
import {ansatte} from '../../ansatte';
import Favicon from '../../images/favicon.png';
import Helmet from 'react-helmet';
import Footer from '../../components/Footer';

const IndexPage = props => {
  console.log (
    props.data.EmployeeImages.edges,
    props.data.EmployeeImages.edges.find (node => node.node.name === 'erik')
  );

  return (
    <Fragment>
      <Helmet
        title="Ansatte i Scelto AS"
        meta={[
          {name: 'description', content: 'Sample'},
          {name: 'keywords', content: 'sample, something'},
        ]}
        link={[{rel: 'icon', href: Favicon}]}
      />
      <Navigation />
      <FullPageImageWithHeader
        image={SmartPeople}
        title="Det er de ansatte som utgjÃ¸r Scelto"
        subtitle="Menneskene i Scelto kjennetegnes ved mer enn bare sin faglig styrke og senior erfaring. En Sceltoer er ogsÃ¥ fremoverlent, nysgjerrig og aldri redd for Ã¥ bidra med sin synsvinkel."
        link="#ansatte"
      />

      <div
        style={{
          display: 'flex',
          flexFlow: 'row wrap',
          justifyContent: 'center',
          margin: '50px 20px 0',
        }}
      >
        {Object.keys (ansatte).sort ().map (key => {
          const {image, name, title} = ansatte[key];
          return (
            <EmployeeImageLink
              style={{}}
              image={props.data.EmployeeImages.edges.find (
                node => node.node.name === name
              )}
              name={name}
              title={title}
              to={`/ansatte/${key}`}
            />
          );
        })}
      </div>
      <Footer />
    </Fragment>
  );
};
export const query = graphql`
  query {
    EmployeeImages: allFile(sort: {order: ASC, fields: [absolutePath]}, filter: {relativePath: {regex: "/mugshots/.*.jpg/"}}) {
      edges {
        node {
          relativePath
          name
          childImageSharp {
            fluid(maxWidth: 1000) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;

export default IndexPage;
